<template>
  <LoadingOverlay :active="isLoading"></LoadingOverlay>
  <div class="container">
    <div class="row mt-4">
      <div class="col-md-7">
        <table class="table align-middle">
          <thead>
          <tr>
            <th>圖片</th>
            <th>商品名稱</th>
            <th>價格</th>
            <th></th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(item) in productList" :key="item.id">
            <td style="width: 200px">
              <div v-if="item.imageUrl" style="height: 100px; background-size: cover; background-position: center; background-repeat:no-repeat;"
              :style="{backgroundImage: `url(${item.imageUrl})`}"></div>
              <div v-else style="height: 100px; background-size: cover; background-position: center; background-repeat:no-repeat; background-image:url('data:image/png;base64,')"></div>
            </td>
            <td><a href="#" class="text-dark">{{ item.title }}</a></td>
            <td>
              <div class="h5" v-if="!item.price">{{ item.origin_price }}元</div>
              <del class="h6" v-if="item.price">原價 {{ item.origin_price }} 元</del>
              <div class="h5" v-if="item.price">現在只要 {{ item.price }} 元</div>
            </td>
            <td>
              <div class="btn-group btn-group-sm">
                <button type="button" class="btn btn-outline-secondary" @click="getProductsDetail(item)">
                  查看更多
                </button>
                <button type="button" class="btn btn-outline-danger" :disabled="this.status.loadingItem === item.id" @click="addToCart(item)">
                  <div v-if="this.status.loadingItem === item.id"
                      class="spinner-grow text-danger spinner-grow-sm" role="status">
                    <span class="visually-hidden">Loading...</span>
                  </div>
                  加到購物車
                </button>
              </div>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
      <!-- 購物車列表 -->
      <div class="col-md-5">
        <div class="sticky-top">
          <table class="table align-middle">
            <thead>
              <tr>
                <th></th>
                <th>品名</th>
                <th style="width: 110px">數量</th>
                <th>單價</th>
              </tr>
            </thead>
            <tbody>
            <template v-if="cartList.carts">
              <tr v-for="item in cartList.carts" :key="item.id">
                <td>
                  <button type="button" class="btn btn-outline-danger btn-sm"
                  :disabled="status.loadingItem === item.id" @click="deleteCart(item)">
                    <i class="bi bi-x"></i>
                  </button>
                </td>
                <td>
                  {{ item.product.title }}
                  <div class="text-success" v-if="item.coupon">
                    已套用優惠券
                  </div>
                </td>
                <td>
                  <div class="input-group input-group-sm">
                    <input type="number" min="1" class="form-control" :disabled="item.id === status.loadingItem" v-model.number="item.qty" @change="updateCart(item)">
                    <div class="input-group-text">/ {{ item.product.unit }}</div>
                  </div>
                </td>
                <td class="text-end">
                  <small v-if="item.final_total !== item.total" class="text-success">折扣價：</small>
                  {{ item.final_total }}
                </td>
              </tr>
            </template>
            </tbody>
            <tfoot>
            <tr>
              <td colspan="3" class="text-end">總計</td>
              <td class="text-end">{{ cartList.total }}</td>
            </tr>
            <tr v-if="cartList.final_total !== cartList.total">
              <td colspan="3" class="text-end text-success">折扣價</td>
              <td class="text-end text-success">{{ cartList.final_total }}</td>
            </tr>
            </tfoot>
          </table>
          <div class="input-group mb-3 input-group-sm">
            <input type="text" class="form-control" placeholder="請輸入優惠碼">
            <div class="input-group-append">
              <button class="btn btn-outline-secondary" type="button">
                套用優惠碼
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      productList: {},
      isLoading: false,
      status: {
        // 對應品項 id
        loadingItem: ''
      },
      cartList: {}
    }
  },
  methods: {
    getProducts () {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products/all`
      this.isLoading = true
      this.$http.get(api)
        .then((res) => {
          if (res.data.success) {
            this.productList = res.data.products
            this.isLoading = false
          } else {
            console.log(res.data.messages)
          }
        })
    },
    getProductsDetail (item) {
      this.$router.push(`/userDashboard/userProduct/${item.id}`)
    },
    addToCart (item) {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`
      this.status.loadingItem = item.id
      const cart = {
        product_id: item.id,
        qty: 1
      }
      this.$http.post(api, { data: cart })
        .then((res) => {
          this.getCartList()
          this.status.loadingItem = ''
        })
    },
    getCartList () {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`
      this.isLoading = true
      this.$http.get(api)
        .then((res) => {
          this.cartList = res.data.data
          this.isLoading = false
        })
    },
    updateCart (item) {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart/${item.id}`
      this.isLoading = true
      this.status.loadingItem = item.id
      const updateData = {
        product_id: item.product_id,
        qty: item.qty
      }
      this.$http.put(api, { data: updateData })
        .then((res) => {
          this.getCartList()
          this.status.loadingItem = ''
        })
    },
    deleteCart (item) {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart/${item.id}`
      this.status.loadingItem = item.id
      this.$http.delete(api)
        .then((res) => {
          this.getCartList()
          this.status.loadingItem = ''
        })
    }
  },
  created () {
    this.getProducts()
    this.getCartList()
  }
}
</script>
